<template>
  <div id="Products">
    <div class="Products">
      <div class="text-right mt-4">
        <button class="btn btn-primary" @click="openModal(true)">建立新的產品</button>
      </div>
      <table class="table mt-4">
        <thead>
          <tr>
            <th width="120">分類</th>
            <th>產品名稱</th>
            <th width="120">原價</th>
            <th width="120">售價</th>
            <th width="100">是否啟用</th>
            <th width="80">編輯</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item) in products" :key="item.id">
            <td>{{ item.category }}</td>
            <td>{{ item.title }}</td>
            <td class="text-right">{{ item.origin_price}}</td>
            <td class="text-right">{{ item.price}}</td>
            <td>
              <span v-if="item.is_enabled" class="text-success">啟用</span>
              <span v-else>未啟用</span>
            </td>
            <td>
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item,true)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="openModal(false,item,false)">刪除</button>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- Modal -->
      <div
        class="modal fade"
        id="productModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div v-if="openSwitch" class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span>新增產品</span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="image">輸入圖片網址</label>
                    <input
                      type="text"
                      class="form-control"
                      id="image"
                      v-model="tempProduct.imageUrl"
                      placeholder="請輸入圖片連結"
                    />
                  </div>
                  <div class="form-group">
                    <label for="customFile">
                      或 上傳圖片
                      <i class="fas fa-spinner fa-spin"></i>
                    </label>
                    <input
                      type="file"
                      id="customFile"
                      class="form-control"
                      ref="files"
                      name="file-to-upload"
                      @change="uploadFile()"
                    />
                  </div>
                  <img
                    img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid"
                    :src="tempProduct.imageUrl"
                    alt
                  />
                </div>
                <div class="col-sm-8">
                  <div class="form-group">
                    <label for="title">標題</label>
                    <input
                      type="text"
                      class="form-control"
                      id="title"
                      v-model="tempProduct.title"
                      placeholder="請輸入標題"
                    />
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category">分類</label>
                      <input
                        type="text"
                        class="form-control"
                        id="category"
                        v-model="tempProduct.category"
                        placeholder="請輸入分類"
                      />
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price">單位</label>
                      <input
                        type="unit"
                        class="form-control"
                        id="unit"
                        v-model="tempProduct.unit"
                        placeholder="請輸入單位"
                      />
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="origin_price">原價</label>
                      <input
                        type="number"
                        class="form-control"
                        id="origin_price"
                        v-model="tempProduct.origin_price"
                        placeholder="請輸入原價"
                      />
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price">售價</label>
                      <input
                        type="number"
                        class="form-control"
                        id="price"
                        v-model="tempProduct.price"
                        placeholder="請輸入售價"
                      />
                    </div>
                  </div>
                  <hr />

                  <div class="form-group">
                    <label for="description">產品描述</label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="description"
                      v-model="tempProduct.description"
                      placeholder="請輸入產品描述"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content">說明內容</label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="content"
                      v-model="tempProduct.content"
                      placeholder="請輸入產品說明內容"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        v-model="tempProduct.is_enabled"
                        :true-value="1"
                        :false-value="0"
                        id="is_enabled"
                      />
                      <label class="form-check-label" for="is_enabled">是否啟用</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
              <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
            </div>
          </div>
        </div>
        <div v-else class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white align-items-center">
              <h5 class="modal-title" id="exampleModalLabel">
                <span>刪除產品</span>
              </h5>
              <div>
                <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                <button type="button" class="ml-3 btn btn-primary" @click="openDelProductModal()">確認</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        products: [],
        tempProduct: {},
        isNew: false,
        openSwitch: true
      };
    },
    methods: {
      //讀取產品列表
      getProducts() {
        const { axios, $store } = this;
        const api = `${$store.state.APIPATH}/api/${$store.state.NAME}/products`;
        axios.get(api).then(res => {
          this.products = res.data.products;
        });
      },
      //打開編輯視窗
      openModal(isNew, item, Del) {
        //刪除視窗
        if (!Del && !isNew) {
          this.tempProduct = Object.assign({}, item);
          this.openSwitch = false;
          $("#productModal").modal("show");
          return;
        }
        //新增修改視窗
        if (isNew) {
          this.tempProduct = {};
          this.isNew = true;
        } else {
          this.tempProduct = Object.assign({}, item);
          this.isNew = false;
        }
        $("#productModal").modal("show");
      },
      openDelProductModal() {
        const { axios, $store, tempProduct } = this;
        let api = `${$store.state.APIPATH}/api/${$store.state.NAME}/admin/product/${tempProduct.id}`;
        axios.delete(api).then(res => {
          console.log(res.data);
          $("#productModal").modal("hide");
          this.getProducts();
        });
      },
      //上傳&修改 新資料
      updateProduct() {
        const { axios, $store, tempProduct, isNew } = this;
        let api = `${$store.state.APIPATH}/api/${$store.state.NAME}/admin/product`;
        if (!isNew) {
          api = `${$store.state.APIPATH}/api/${$store.state.NAME}/admin/product/${tempProduct.id}`;
        }
        axios[isNew ? "post" : "put"](api, { data: tempProduct }).then(res => {
          console.log(res.data);
          if (res.data.success) {
            $("#productModal").modal("hide");
            this.getProducts();
          } else {
            $("#productModal").modal("hide");
            this.getProducts();
            console.log("新增失敗");
          }
          this.openSwitch = true;
        });
      },
      //上傳圖片
      uploadFile() {
        const { axios, $store } = this;
        const uploadedFile = this.$refs.files.files[0];
        const formData = new FormData();
        formData.append("file-to-upload", uploadedFile);
        const api = `${$store.state.APIPATH}/api/${$store.state.NAME}/admin/upload`;
        axios
          .post(api, formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(res => {
            console.log(res.data);
            if (res.data.success) {
              // this.tempProduct.imageUrl = res.data.imageUrl;
              this.$set(this.tempProduct, "imageUrl", res.data.imageUrl);
            }
          });
      }
    },
    created() {
      this.getProducts();
    }
  };
</script>
<style lang="scss">
  @import "./Products.scss";
</style>
